
html {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1B997A;
  margin-top: 60px;
  margin: 0 30px 0 30px;
  padding: 0;
  font-family: sans-serif;
  display: grid;
  grid-template-rows: 15% 75% 10%;
}
main {
  grid-row: 2/3;
  width: 70%;
  margin: 0 auto;
  padding: 10px;
}
small {
  color: black;
  font-size: .5rem;
  margin-left: 10px;
}
a {
  color: #C261CC;
  text-decoration: none;
}
.hidden {
  display: none;
}



















.header {
   grid-row: 1 / 2;
   display: grid;
   grid-template-columns: 25% 50% 15%;
   grid-template-rows: 50% 50%;
   padding-top: 10px;
}
img {
   grid-row: 1/3;
   grid-column: 1/2;
   height: 100px;
   width: auto;
}

.footer {
   position: fixed;
   bottom: 0;
   padding: 5px;
}
small {
   margin: 0 auto;
}






















form {
  display: block;
}

#application-preview {
  border-radius: 6px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  border: 1px solid darkgrey;
  min-height: 100px;
  margin-top: 20px;
  padding: 10px;
}
